$material-icons-family: 'Material Icons Outlined';

@font-face {
    font-family: $material-icons-family;
    font-style: normal;
    font-weight: 400;
    src:
        local($material-icons-family),
        local("MaterialIcons-Outlined"),
        url('./fonts/global/Material_Icons_Outlined.woff2') format('woff2'),
        url('./fonts/global/Material_Icons_Outlined.woff') format('woff'),
        url('./fonts/global/Material_Icons_Outlined.eot?#iefix') format('embedded-opentype');
}

.material-icons, .fc .fc-button {
    font-family: $material-icons-family;
    font-weight: normal;
    font-style: normal;
    font-size: 24px;
    line-height: 1;
    letter-spacing: normal;
    text-transform: none;
    display: inline-block;
    overflow: hidden;
    white-space: nowrap;
    word-wrap: normal;
    direction: ltr;
    -webkit-font-smoothing: antialiased;
    text-rendering: optimizeLegibility;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-feature-settings: 'liga';
    font-feature-settings: 'liga';
}

.fc .fc-button{
    font-family: $material-icons-family !important;
}

@font-face {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 300;
    src:
        url('./fonts/global/Roboto_300.woff2') format('woff2'),
        url('./fonts/global/Roboto_300.woff') format('woff'),
        url('./fonts/global/Roboto_300.eot?#iefix') format('embedded-opentype'),
        url('./fonts/global/Roboto_300.svg#Roboto') format('svg'),
        url('./fonts/global/Roboto_300.ttf') format('truetype');
}

